.shadow-wrapper {
  background: #FFFFFF;
  border: 1px solid #EAEAEA;
  box-shadow: 0 5px 30px 0 rgba(13,27,42,0.10);
  padding: 21px 27px 21px 27px;
}

.company-addresses {
  margin-top: 40px;
}
.company-addresses .company-address-col {
  margin-top: 15px;
}
.company-addresses .company-address-col:hover .company-address-row-edit {
  opacity: 1;
}
.company-addresses .company-address-row-edit {
  opacity: 0;
  cursor: pointer;
  display: flex;
  width: 30px;
  height: 25px;
  justify-content: center;
  align-items: center;
}
.company-addresses .company-address-row-edit svg {
  color: #989CA8;
}
.company-addresses .company-address-row-title {
  font-weight: 500;
  font-size: 14px;
  color: #0d1b2a;
  letter-spacing: 0;
  display: flex;
}
.company-addresses .company-address-row-title span {
  flex: 1 1;
}
.company-addresses .company-address-row {
  font-weight: 500;
  font-size: 14px;
  color: #989CA8;
  letter-spacing: 0;
}

.company-infos-subtitle {
  font-weight: 700;
  font-size: 10px;
  color: #8a8a8a;
  letter-spacing: 0;
  display: block;
  text-transform: uppercase;
  margin-top: 33px;
}
.company-infos-value {
  font-weight: 400;
  font-size: 16px;
  color: #0d1b2a;
  letter-spacing: 0;
  display: block;
}

.form-marketing h3 {
  color: #0053FA;
  margin-top: 40px;
}

.infos-form-title {
  font-weight: 600;
  font-size: 20px;
  color: #0053FA;
  letter-spacing: 0;
  margin-bottom: 20px;
}
.ant-collapse {
  margin-top: 38px;
}
.ant-collapse .card-title {
  text-align: left;
  font-weight: 600;
  text-transform: uppercase;
}
.ant-collapse .company_address_row {
  text-align: left;
  font-weight: 500;
  font-size: 14px;
  letter-spacing: 0;
}

